}

:root {
  --main-font: "Roboto", sans-serif;
  --main-txt-cl: #000;
  --accent-txt-cl: #b92f2c;
  --btn-txt-cl: #000;
  --btn-txt-cl: #fff;
  --copyright-txt-cl: #545454;
  --copyright-txt-cl-dt: #f1f1f1;
  --modal-title: #8c8c8c;
  --bg-cl: #fff;
  --footer-bg-cl: #f7f7f7;
  --btn-bg-color: #000;
  --btn-radius: 100px;
  --duration: 250ms;
  --timing-function: linear;
  --trns-fast: 300ms ease-in-out;
  --trns-slow: 500ms ease-in;
}

.visually-hidden {
  white-space: nowrap;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  border: 0;
  margin: -1px;
  padding: 0;
